const topcell: HTMLElement = document.getElementById("topcell");
const cells: HTMLElement[]=[
    document.getElementById("topleftcell"),
    document.getElementById("topmiddlecell"),
    document.getElementById("toprightcell"),
    document.getElementById("middlerightcell"),
    document.getElementById("middleleftcell"),
    document.getElementById("middlemiddlecell"),
    document.getElementById("bottomrightcell"),
    document.getElementById("bottomleftcell"),
    document.getElementById("bottommiddlecell")]
const text:HTMLElement = document.getElementById("text");

let currentPlayer = "X";
for (const cell of cells){
    cell.addEventListener("click",(e) => {
        if (cell.textContent==="") {
            cell.textContent = currentPlayer;
            if (currentPlayer==="X"){
                text.textContent="Player O's turn"
                currentPlayer="O";
            } else{
                text.textContent="Player X's turn"
                currentPlayer="X";
            }
        }
    });
}


